<template>
  <div class="container">
    <IndexHeader>二维码名片生成</IndexHeader>
    <QrEditor :qrText="qrText">
      <template #contentTitle>输入内容，开始制作二维码</template>
      <template #tips>功能描述：生成电子名片二维码。</template>
      <template #help>
        <p><span>1. 有效期：</span>本页面电子名片是静态二维码，生成后永久有效。</p>
        <p>
          <span>2. 二维码名片原理</span>：二维码电子名片本质是将您输入的信息转为
          VCard/MeCard，并转存到二维码中，通过微信扫描二维码即可快捷地将名片信息存储到手机通讯录中。
        </p>
        <p>【VCard】是电子名片的文件格式标准，文件后缀是vcf，广泛用于在互联网和智能手机上交换联系人信息。</p>
        <p>【MeCard】也是一种电子名片格式。虽然流行度不如VCard，但市场上大多数智能手机都支持。</p>
      </template>
      <template #content>
        <a-form-item label="电子名片格式" :labelCol="{ span: 3 }" style="margin-bottom: 8px">
          <a-radio-group name="ecardFormat" v-model:value="format">
            <a-radio value="vcard">VCard 3.0（常用）</a-radio>
            <a-radio value="mecard">MeCard</a-radio>
          </a-radio-group>
        </a-form-item>
        <div class="vcard-form">
          <a-input v-model:value="name" addon-before="姓名" placeholder="姓名" allowClear />
          <a-input v-model:value="email" addon-before="邮箱" placeholder="邮箱" allowClear />
          <a-input v-model:value="cell" addon-before="手机" placeholder="手机" allowClear />
          <a-input
            v-model:value="org"
            addon-before="公司"
            placeholder="公司"
            allowClear
            v-if="format === 'vcard'"
          />
          <a-input
            v-model:value="title"
            addon-before="职位"
            placeholder="职位"
            allowClear
            v-if="format === 'vcard'"
          />
          <a-input
            v-model:value="tel"
            addon-before="工作电话"
            placeholder="工作电话"
            allowClear
            v-if="format === 'vcard'"
          />
          <a-input
            class="middle"
            v-model:value="addr"
            placeholder="地址"
            allowClear
            v-if="format === 'vcard'"
          >
            <template #addonBefore>
              <a-select v-model:value="addrType" style="width: 100px">
                <a-select-option value="work">工作地址</a-select-option>
                <a-select-option value="home">家庭地址</a-select-option>
              </a-select>
            </template>
          </a-input>
          <a-input
            class="middle"
            v-model:value="addr"
            addon-before="地址"
            placeholder="地址"
            allowClear
            v-else
          />
          <a-input v-model:value="url" addon-before="个人主页" placeholder="个人主页" allowClear />
          <a-input
            class="big"
            v-model:value="note"
            addon-before="备注信息"
            placeholder="备注信息"
            allowClear
          />
        </div>
      </template>
    </QrEditor>
  </div>
</template>

<script>
import IndexHeader from '@/components/IndexHeader.vue';
import QrEditor from '@/components/QrEditor/QrEditor.vue';
import { computed, reactive, toRefs } from 'vue';
import { useHead } from '@vueuse/head';

export default {
  name: 'Ecard',
  components: {
    IndexHeader,
    QrEditor
  },
  setup() {
    useHead({
      title: '二维码电子名片生成 - 兔子二维码',
      meta: [
        { name: 'keywords', content: '二维码名片,电子名片,VCard,MeCard' },
        {
          name: 'description',
          content: '在线制作VCard,MeCard电子名片二维码'
        }
      ]
    });

    let view = reactive({
      name: '',
      email: '',
      cell: '',
      org: '',
      title: '',
      tel: '',
      addr: '',
      addrType: 'work',
      url: '',
      note: '',
      format: 'vcard'
    });

    let qrText = computed(() => {
      let content = [];
      if (view.format === 'vcard') {
        view.name && content.push(`FN:${view.name}`);
        view.email && content.push(`EMAIL:${view.email}`);
        view.cell && content.push(`TEL;TYPE=cell:${view.cell}`);
        view.title && content.push(`TITLE:${view.title}`);
        view.org && content.push(`ORG:${view.org}`);
        view.tel && content.push(`TEL;TYPE=work:${view.tel}`);
        view.addr && content.push(`ADR;TYPE=${view.addrType}:${view.addr}`);
        view.url && content.push(`URL:${view.url}`);
        view.note && content.push(`NOTE:${view.note}`);
        if (!content.length) return '';
        content.unshift(`VERSION:3.0`);
        content.unshift(`BEGIN:VCARD`);
        content.push(`END:VCARD`);
      } else if (view.format === 'mecard') {
        view.name && content.push(`N:${view.name};`);
        view.email && content.push(`EMAIL:${view.email};`);
        view.cell && content.push(`TEL:${view.cell};`);
        view.addr && content.push(`ADR:${view.addr};`);
        view.url && content.push(`URL:${view.url};`);
        view.note && content.push(`NOTE:${view.note};`);
        if (!content.length) return '';
        content.unshift(`MECARD:`);
      }
      return content.join('\n');
    });

    return {
      ...toRefs(view),
      qrText
    };
  }
};
</script>

<style lang="less" scoped>
.vcard-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  span {
    width: 32%;
    margin-bottom: 10px;
    &.middle {
      width: 66%;
    }
    &.big {
      width: 100%;
    }
  }
}
</style>
